import React from 'react'; //rsc
import s from './Login.module.css';
import LoginForm, { LoginFormOwnProps } from './LoginForm/LoginForm';
import { reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { login } from '../../redux/authReducer';
import { Navigate } from 'react-router-dom';
import { AppStateType } from '../../redux/reduxStore';

let LoginReduxForm = reduxForm<LoginFormDataType, LoginFormOwnProps>({
  form: 'login',
})(LoginForm);

export type LoginFormDataType = {
  email: string;
  password: string;
  captcha: string;
  remember: boolean;
};

const Login: React.FC<MapStateType & MapDispatchType> = (props) => {
  let onSubmit = (formData: any) => {
    props.login(
      formData.email,
      formData.password,
      formData.remember,
      formData.captcha,
    );
  };

  if (props.isAuth) {
    return <Navigate to="/profile" />;
  }

  return (
    <div className={s.content}>
      моя логинка
      <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl} />
    </div>
  );
};

type MapStateType = {
  isAuth: boolean;
  captchaUrl: string | null;
};
type MapDispatchType = {
  login: (
    email: string,
    password: string,
    remember: boolean,
    captcha: string,
  ) => void;
};

let mapStateToProps = (state: AppStateType): MapStateType => ({
  isAuth: state.auth.isAuth,
  captchaUrl: state.auth.captchaUrl,
});

export default connect(mapStateToProps, { login })(Login);
